import React, { useEffect, useState, useRef } from "react";

import { IWorkflowStep } from "../../types/workflow.type";

const WorkflowStep = ({
  step,
  scroll,
  stepNumber,
  className,
}: {
  step: IWorkflowStep;
  scroll: boolean;
  stepNumber: number;
  className?: string;
}) => {
  const div = useRef<HTMLDivElement>(null);
  const [shownClass, setShownClass] = useState<"workflow-step-shown" | "opacity-0">("opacity-0");

  useEffect(() => {
    if (!div.current || !window) return;
    const divBottom = div.current.getBoundingClientRect().bottom;

    if (window.innerHeight > divBottom) setShownClass("workflow-step-shown");
  }, [scroll]);

  const {
    html,
    frontmatter: { title },
  } = step;

  return (
    <div
      ref={div}
      className={`workflow-step relative flex w-full items-center justify-between text-secondary-100 gap-5 md:gap-28 max-w-4.5xl ${className} ${shownClass}`}
    >
      <p className="text-center px-5 md:px-20 text-[88px] sm:text-[148px] font-bold shrink-0 py-4 text-primary-70">
        {stepNumber}
      </p>
      <div className="flex flex-col flex-1 gap-2.5">
        <h2 className="font-bold text-2xl sm:text-3xl">{title}</h2>
        <div
          className="w-full font-normal text-lg"
          dangerouslySetInnerHTML={{ __html: html }}
        />
      </div>
    </div>
  );
};

export default WorkflowStep;
