import React, { PropsWithChildren } from "react";
import { Link } from "gatsby";

import { Theme } from "../../types/common.type";

import Border from "../../assets/common/border.svg";

import ContentContainer from "./contentContainer";

interface ITemplateSectionProps extends React.HTMLAttributes<HTMLElement> {
  title: string;
  theme: Theme;
  isArrowLink?: boolean;
  linkTitle?: string;
  subTitle?: string;
  section?: string;
  className?: string;
  borderT?: boolean;
  borderB?: boolean;
}

const TemplateSection = ({
  section,
  theme,
  title,
  linkTitle,
  subTitle,
  className,
  children,
  borderT,
  borderB,
  isArrowLink = true,
  ...attr
}: PropsWithChildren<ITemplateSectionProps>) => {
  const mainColorClass =
    theme === Theme.dark ? "text-secondary-100" : "text-main-100";
  const linkColorClass =
    theme === Theme.dark
      ? "text-secondary-100 hover:text-secondary-70"
      : "text-main-100 hover:text-main-10";

  return (
    <>
      {borderT ? <Border className="w-full" /> : null}
      <section
        className={`w-full py-16 bg-transparent ${className}`}
        {...attr}
      >
        <ContentContainer
          maxWidthClass="max-w-[1280px]"
          className={`flex-col items-center ${mainColorClass}`}
        >
          <div className="flex justify-between w-full sm:items-center pb-8 sm:pb-14 flex-col sm:flex-row gap-5">
            <h2 className={`text-4xl font-bold flex-1 ${mainColorClass}`}>
              {title.toUpperCase()}
            </h2>
            {section ? (
              <Link
                to={`/${section}`}
                className={`w-full sm:w-auto text-start sm:text-end text-xl font-normal cursor-pointer whitespace-nowrap ${linkColorClass}`}
              >
                {linkTitle ? linkTitle.toUpperCase() : "DISCOVER MORE"}{" "}
                {isArrowLink ? <>&#8594;</> : null}
              </Link>
            ) : null}
            {subTitle ? (
              <p
                className={`w-full sm:w-auto flex-1 text-start sm:text-end text-xl font-normal ${mainColorClass}`}
              >
                {subTitle.toUpperCase()}
              </p>
            ) : null}
          </div>
          {children}
        </ContentContainer>
      </section>
      {borderB ? <Border className="w-full" /> : null}
    </>
  );
};

export default TemplateSection;
